import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { HeaderComponent } from './header/header.component';
import { HotelAndResortComponent } from './hotel-and-resort/hotel-and-resort.component';
import { InstitutionalComponent } from './institutional/institutional.component';
import { ResidentialComponent } from './residential/residential.component';
import { TownHouseComponent } from './town-house/town-house.component';
import { PortfolioComponent } from './portfolio/portfolio.component';


const routes: Routes = [
  {
    path:'',
    redirectTo:'portfolio',
    pathMatch:'full'
  },
  {
    path: 'about-us',
    component: AboutUsComponent
  },
  {
    path: 'contact-us',
    component: ContactUsComponent
  },
  {
    path: 'header',
    component: HeaderComponent
  },
  {
    path: 'portfolio/hotel-and-resort',
    component: HotelAndResortComponent
  },
  {
    path: 'portfolio/institutional',
    component: InstitutionalComponent
  },
  {
    path: 'portfolio',
    component:  PortfolioComponent
  },
  {
    path: 'portfolio/town-house',
    component: TownHouseComponent
  },
  {
    path: 'portfolio/residential',
    component: ResidentialComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
